import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { crypt } from './EncDec';
import SearchRegEx from './validation/SearchRegEx';


const SearchBox = ({srch}) => {
    const navigate = useNavigate();
    const [mainQuer, setMainQuer] = useState('Sell');
    const [prvComm, setPrvComm] = useState('Private');
    const [search, setSearch] = useState('');
    const handleClick = (e) => {
        e.preventDefault();
        if(SearchRegEx(mainQuer+" "+prvComm+" "+search)) {
            navigate("/searchresult/"+crypt("myRealsujan", mainQuer+"?"+prvComm+"?"+search));
        }
        // console.log(prvComm);
    }
    useEffect(()=>{
        if(srch) {
            setSearch(srch.query3);
            setMainQuer(srch.query1);
            setPrvComm(srch.query2);
            // console.log(srch.query1, srch.query2);
        }
    },[]);
    return (
        <form className="main-search-form" onSubmit={handleClick} autoComplete='off'>
            <div className="search-type">
                <div className="st-item">
                    <input defaultChecked={!srch?true:srch.query1 === 'Sell'?true:false} type="radio" name="st" value="Sell" onChange={(e)=>setMainQuer(e.target.value)} id="buy" />
                    <label htmlFor="buy">Buy</label>
                </div>
                <div className="st-item">
                    <input type="radio" defaultChecked={srch && srch.query1 === 'rent_out'?true:false} name="st" value="rent_out" onChange={(e)=>setMainQuer(e.target.value)} id="rent" />
                    <label htmlFor="rent">Rent</label>
                </div>
                <div className="st-item">
                    <input type="radio" defaultChecked={srch && srch.query1 === 'Upcoming'?true:false} name="st" value="Upcoming" onChange={(e)=>setMainQuer(e.target.value)} id="projects" />
                    <label htmlFor="projects">Upcoming Projects</label>
                </div>
            </div>
            <div className="search-input">
                <input type="text" name="searchField" defaultValue={srch?srch.query3:''} onChange={(e)=>setSearch(e.target.value)} placeholder="Search by state, postcode or suburb" />
                <button className="site-btn">Search</button>
            </div>
            <div className="custom-control custom-radio ib_radio">
                <input type="radio" defaultChecked={!srch?true:srch.query2 === 'Private'?true:false} id="Private" name="prvComm" value="Private" onChange={(e)=>setPrvComm(e.target.value)} className="custom-control-input" />
                <label className="custom-control-label" htmlFor="Private">Private</label>
            </div>
            <div className="custom-control custom-radio ib_radio">
                <input type="radio" defaultChecked={srch && srch.query2 === 'Commercial'?true:false} id="Commercial" name="prvComm" value="Commercial" onChange={(e)=>setPrvComm(e.target.value)} className="custom-control-input" />
                <label className="custom-control-label" htmlFor="Commercial">Commercial</label>
            </div>
            <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Quis ipsum suspendisse ultrices gravida. </p>
        </form>
    );
}
export default SearchBox;