import React, { useEffect, useState } from 'react';
import SearchBox from './SearchBox';
import ImageGallery from 'react-image-gallery';
import { useParams, Link, useNavigate } from 'react-router-dom';
import axios from 'axios';
import AnimateError from './validation/AnimateError';
import Back_URL from './validation/BackendUrl';
import ValidateMobile from './validation/ValidateMobile';

export default function Property() {

    const localUser = localStorage.getItem("token");
    const { id } = useParams();
    const [ploader, setPloader] = useState(true);
    const [noItem, setNoitem] = useState(false);
    const [sldImages, setslideImages] = useState([]);
    const [Emi, setEmi] = useState(0);
    let [pdata, setPdata] = useState({
        _id:"",
        price: "",
        place: "",
        pin: "",
        city: "",
        house_type: "",
        description: "",
        details: "",
        hse_for: "",
        stories: "",
        year_build: "",
        A_C: "",
        heating: "",
        bathrooms: "",
        pool: "",
        fireplace: "",
        parking_space: "",
        parking_type: "",
        patio: "",
        playground: "",
        yt_url: "",
        g_map_url: "",
        img_detail: [],
        status: "",
        want_to: "",
        property_type: "",
        bedrooms: "",
        property_area: "",
    });
    useEffect(() => {
        axios.get('api/propertydetail/'+id)
        .then(function (response) {
            setPdata({
                _id: response.data._id,
                price: response.data.price,
                place: response.data.place,
                pin: response.data.pin,
                city: response.data.city,
                house_type: response.data.house_type,
                description: response.data.description,
                details: response.data.details,
                hse_for: response.data.hse_for,
                stories: response.data.stories,
                year_build: response.data.year_build,
                A_C: response.data.A_C,
                heating: response.data.heating,
                bathrooms: response.data.bathrooms,
                pool: response.data.pool,
                fireplace: response.data.fireplace,
                parking_space: response.data.parking_space,
                parking_type: response.data.parking_type,
                patio: response.data.patio,
                playground: response.data.playground,
                yt_url: response.data.yt_url,
                g_map_url: response.data.g_map_url,
                img_detail: response.data.img_detail,
                status: response.data.status,
                want_to: response.data.want_to,
                property_type: response.data.property_type,
                bedrooms: response.data.bedrooms,
                property_area: response.data.property_area,
            });
            setPloader(false);
            if(response.data.user_upload_status === "open") {
                setNoitem(false);
            } else {
                setNoitem(true);
            }
            const slideImages = pdata.img_detail.map((items)=>{
                let rObj = {}
                rObj["original"] = Back_URL+items;
                rObj["thumbnail"] = Back_URL+items;
                return rObj
            });
            setslideImages(slideImages);
        })
        .catch(function (error) {
            setPloader(false);
            setNoitem(true);
        });
    },[pdata._id]);

    // for loan calculator
    const [purprice, setPurprice] = useState('');
    const [irate, setIrate] = useState('');
    const [downPay, setDownpay] = useState('');
    const [monterms, setMonterms] = useState('');
    const [calcError, setCalcerror] = useState({
        epurprice: '',
        eirate: '',
        edownPay: '',
        emonterms: ''
    });
    const loanCalc = (e) => {
        e.preventDefault();
        let errorCount = 0;
        if(purprice === '' || purprice < 1000 || isNaN(purprice) || !Number.isInteger(parseFloat(purprice))) {
            errorCount++;
            setCalcerror((prevState)=> {
                return {...prevState, epurprice: 'Not Valid'}
            });
            AnimateError();
        } else {
            setCalcerror((prevState)=> {
                return {...prevState, epurprice: ''}
            });
        } if(irate === '' || irate > 30 || isNaN(irate)) {
            errorCount++;
            setCalcerror((prevState)=> {
                return {...prevState, eirate: 'Not Valid'}
            });
            AnimateError();
        } else {
            setCalcerror((prevState)=> {
                return {...prevState, eirate: ''}
            });
        } if(parseFloat(purprice) < parseFloat(downPay) || isNaN(downPay)) {
            errorCount++;
            setCalcerror((prevState)=> {
                return {...prevState, edownPay: 'Not Valid'}
            });
            AnimateError();
        } else {
            setCalcerror((prevState)=> {
                return {...prevState, edownPay: ''}
            });
        } if(monterms === '' || monterms > 240 || isNaN(monterms) || !Number.isInteger(parseFloat(monterms))) {
            errorCount++;
            setCalcerror((prevState)=> {
                return {...prevState, emonterms: 'Not Valid'}
            });
            AnimateError();
        } else {
            setCalcerror((prevState)=> {
                return {...prevState, emonterms: ''}
            });
        }
        if(errorCount === 0) {
            const amount = purprice - downPay;
            const intRate = (amount * (irate * 0.01)) / monterms;
            const EmiCalc = ((amount / monterms) + intRate);
            setEmi(parseInt(EmiCalc));
        }
    }

    const [srvErr, setSrvErr] = useState([]);
    const [ingFrm, setIngFrm] = useState({msge:"", open:true, btn:false});
    const [inqName, setInqName] = useState('');
    const [inqPhone, setInqPhone] = useState('');
    const [inqForm, setInqForm] = useState({
        inqName: '',
        inqPhone: ''
    });
    const quickInq = (e) => {
        e.preventDefault();
        let errorCount = 0;
        if(inqName === '') {
            errorCount++;
            setInqForm((prevState)=> {
                return {...prevState, inqName: 'Name is required'}
            });
            AnimateError();
        } else if(inqName.length < 2) {
            setInqForm((prevState)=> {
                return {...prevState, inqName: 'Name Should be minimum 2 letters'}
            });
            AnimateError();
        } else {
            setInqForm((prevState)=> {
                return {...prevState, inqName: ''}
            });
        } if(inqPhone === '') {
            errorCount++;
            setInqForm((prevState)=> {
                return {...prevState, inqPhone: 'Phone is required'}
            });
            AnimateError();
        } else if(ValidateMobile(inqPhone)){
            errorCount++;
            setInqForm((prevState)=> {
                return {...prevState, inqPhone: 'Mobile Number is not valid'}
            });
            AnimateError();
        } else {
            setInqForm((prevState)=> {
                return {...prevState, inqPhone: ''}
            });
        }
        if(errorCount === 0) {
            setIngFrm({msge:"", open:true, btn:true});
            setSrvErr("");
            axios.post('api/propinquiry',{name: inqName, phone: inqPhone, prop_id: id})
            .then(function (res) {
                setIngFrm({msge:res.data.msg, open:false, btn:false});
            })
            .catch(function (err) {
                setIngFrm({msge:"", open:true, btn:false});
            });
        }
    }

    let navigate = useNavigate();
    const [offerVal, setOfferVal] = useState('');
    const [offerErr, setOfferErr] = useState({
        offerVal: ''
    });
    const [rentoffer, setRentOffer] = useState(false);
    const [offrBtn, setOffrBtn] = useState(false);
    const offerClick = () => {
        setRentOffer(true)
    }
    const sendOffer = (e) => {
        e.preventDefault();
        let errorCount = 0;
        if(offerVal === '') {
            errorCount++;
            setOfferErr((prevState)=> {
                return {...prevState, offerVal: 'Value is required'}
            });
            AnimateError();
        } else if(isNaN(offerVal) || offerVal < 1) {
            errorCount++;
            setOfferErr((prevState)=> {
                return {...prevState, offerVal: 'Value is not valid'}
            });
            AnimateError();
        } else if(offerVal >= pdata.price) {
            errorCount++;
            setOfferErr((prevState)=> {
                return {...prevState, offerVal: 'Offer value must less than the Property value'}
            });
            AnimateError();
        } else {
            setOfferErr((prevState)=> {
                return {...prevState, offerVal: ''}
            });
        }
        if(errorCount === 0) {
            setOffrBtn(true);
            if(localUser) {
                axios.post('api/propoffer', {offerVal: offerVal, prop_id: id}, {
                    headers: {
                        "x-access-token" : localUser,
                        "Content-Type" : "application/json"
                    }
                })
                .then(function (res) {
                    setOffByMe(true);
                    setRentOffer(false);
                    setOffrBtn(false);
                })
                .catch(function (err) {
                    setOffByMe(false);
                    setRentOffer(true);
                    setsrvofferErr(err.response.data);
                    setOffrBtn(false);
                });
            } else {
                navigate("/login");
                setOffrBtn(false);
            }
        }
    }
    const [offByMe, setOffByMe] = useState(false);
    const [shortlist, setShortlist] = useState(false);
    const [srvofferErr, setsrvofferErr] = useState([]);
    useEffect(() => {
        if(localUser) {
            axios.post('api/useroffered',{prop_id: id}, {
                headers: {
                    "x-access-token" : localUser,
                    "Content-Type" : "application/json"
                }
            })
            .then(function (res) {
                setOffByMe(res.data.offer);
                setShortlist(res.data.shortlist);
            })
            .catch(function (err) {
                setOffByMe(false);
                setShortlist(false);
            });
        }
    },[]);
    const [shrtBtn, setShrtBtn] = useState(false);
    const shrtlstProp = () => {
        setShrtBtn(true);
        if(localUser) {
            axios.post('api/shortlistprop', {prop_id: id}, {
                headers: {
                    "x-access-token" : localUser,
                    "Content-Type" : "application/json"
                }
            })
            .then(function (res) {
                if(res.data.msg === "Shortlisted") {
                    setShortlist(true);
                } else {
                    setShortlist(false);
                }
                setShrtBtn(false);
            })
            .catch(function (err) {
                setShortlist(false);
                setShrtBtn(false);
            });
        } else {
            navigate("/login");
            setShrtBtn(false);
        }
    }
    return (
        <>
            {ploader?<div id="preloder"><div className="loader"></div></div>:<div>{!noItem?<div>
                <section className="page-top-section set-bg" style={{ backgroundImage: "url('/img/page-top-bg.jpg')"}}>
                    <div className="page-top-warp">
                        <SearchBox />
                    </div>
                </section>

                <section className="single-property-section spad">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-8">
                                <div className="single-property">
                                    <div className="sp-image">
                                        
                                        {sldImages.length === 1?<img className='responsive_img' src={sldImages[0].original} />:
                                        <ImageGallery
                                            infinite={false}
                                            showFullscreenButton={false}
                                            showPlayButton={false}
                                            showNav={false}
                                            lazyLoad={true}
                                            items={sldImages}
                                        />}
                                        {pdata.status === "New"?<div className="sp-badge new">New</div>:pdata.status === "Offer"?<div className="sp-badge offer">Offer</div>:""}
                                    </div>
                                    <div className="row">
                                        <div className="col-lg-8">
                                            <div className="property-header">
                                                <h3>{pdata.place}</h3>
                                                <h5>{pdata.city}, {pdata.pin} </h5>
                                            </div>
                                        </div>
                                        <div className="col-lg-4 text-left text-lg-right">
                                            <div className="property-header">
                                                <h3><i className="fas fa-rupee-sign"></i>{pdata.price.toLocaleString()}{pdata.want_to==="rent_out"?<span className='per_month'>/Month</span>:""}</h3>
                                                {pdata.property_type === "Private"?<p>{pdata.bedrooms !== "N/A" && pdata.bedrooms !== "1"? pdata.bedrooms+" Bedrooms": pdata.bedrooms === "1"? pdata.bedrooms+" Bedroom":""}  {pdata.house_type}</p>:
                                                pdata.property_type === "Commercial"?<p>{pdata.patio === "N/A"?pdata.patio:pdata.patio+" SF"} {pdata.house_type}</p>:
                                                <p>{pdata.property_type}</p>}
                                            </div>
                                        </div>
                                    </div>
                                    <div className="property-info-bar">
                                        <div className="row">
                                            <div className="col-lg-7">
                                                {pdata.property_type === "Private" || pdata.property_type === "Upcoming Private Project"?
                                                <div className="pi-metas">
                                                    {pdata.bedrooms !== "N/A"?<div className="pi-meta">{pdata.bedrooms} Bed</div>:""}
                                                    {pdata.bathrooms !== "N/A" && pdata.bathrooms !== "No"?<div className="pi-meta">{pdata.bathrooms} Bath</div>:""}
                                                    {pdata.parking_type === "Garage"?<div className="pi-meta">Garage</div>:""}
                                                    <div className="pi-meta">{pdata.property_area+" SF"}</div>
                                                </div>:
                                                <div className="pi-metas"><div className="pi-meta">For {pdata.hse_for}</div></div>}
                                            </div>
                                            {offByMe?<div className="col-lg-5 text-left text-lg-right">
                                                <a className="offer-btn disabled">Offered</a>
                                            </div>:
                                            <div className="col-lg-5 text-left text-lg-right">
                                                {pdata.want_to==="Sell"?localUser?<a onClick={offerClick} className="offer-btn">Offer to Buy</a>:
                                                <Link to="/login" className="offer-btn">Offer to Buy</Link>:
                                                pdata.want_to==="rent_out"?localUser?<a onClick={offerClick} className="offer-btn">Offer to Rent</a>:
                                                <Link to="/login" className="offer-btn">Offer to Buy</Link>:""}
                                            </div>}
                                        </div>
                                        {rentoffer?<div className='row'>
                                            <div className="col-lg-12">
                                                <div className='offering_wrap form_wr_input'>
                                                    <form autoComplete="off" onSubmit={sendOffer}>
                                                        <input type="text" className='form_input_style' onChange={(e)=>setOfferVal(e.target.value)} placeholder="Your Offer" />
                                                        <span className="form_err">{offerErr.offerVal}</span>
                                                        <button disabled={offrBtn} className='site-btn sb-big'>Send Offer</button>
                                                    </form>
                                                </div>
                                            </div>
                                        </div>:""}
                                        {srvofferErr && srvofferErr.map(err =>
                                            <div key={err.msg} className="form_err">{err.msg}</div>
                                        )}
                                    </div>
                                    <div className="property-text">
                                        {pdata.description? <div>
                                            <h4>Description</h4>
                                            <p>{pdata.description}</p>
                                        </div>:""}
                                        {pdata.details? <div>
                                            <h4>Details</h4>
                                            <p>{pdata.details}</p>
                                        </div>:""}
                                    </div>
                                    <div className="property-feature">
                                        <div className="row">
                                            <div className="col-6 col-sm-4 col-md-3 col-lg-2">
                                                <div className="pf-box">
                                                    <h6>For</h6>
                                                    <p>{pdata.hse_for}</p>
                                                </div>
                                            </div>
                                            <div className="col-6 col-sm-4 col-md-3 col-lg-2">
                                                <div className="pf-box">
                                                    <h6>Stories</h6>
                                                    <p>{pdata.stories}</p>
                                                </div>
                                            </div>
                                            <div className="col-6 col-sm-4 col-md-3 col-lg-2">
                                                <div className="pf-box">
                                                    <h6>Year Built</h6>
                                                    <p>{pdata.year_build}</p>
                                                </div>
                                            </div>
                                            <div className="col-6 col-sm-4 col-md-3 col-lg-2">
                                                <div className="pf-box">
                                                    <h6>A/C</h6>
                                                    <p>{pdata.A_C}</p>
                                                </div>
                                            </div>
                                            <div className="col-6 col-sm-4 col-md-3 col-lg-2">
                                                <div className="pf-box">
                                                    <h6>Heating</h6>
                                                    <p>{pdata.heating}</p>
                                                </div>
                                            </div>
                                            <div className="col-6 col-sm-4 col-md-3 col-lg-2">
                                                <div className="pf-box">
                                                    <h6>Bathrooms</h6>
                                                    <p>{pdata.bathrooms}</p>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="row">
                                            <div className="col-6 col-sm-4 col-md-3 col-lg-2">
                                                <div className="pf-box">
                                                    <h6>Pool</h6>
                                                    <p>{pdata.pool}</p>
                                                </div>
                                            </div>
                                            <div className="col-6 col-sm-4 col-md-3 col-lg-2">
                                                <div className="pf-box">
                                                    <h6>Fireplace</h6>
                                                    <p>{pdata.fireplace}</p>
                                                </div>
                                            </div>
                                            <div className="col-6 col-sm-4 col-md-3 col-lg-3">
                                                <div className="pf-box">
                                                    <h6>Parking Spaces</h6>
                                                    <p>{pdata.parking_space}</p>
                                                </div>
                                            </div>
                                            <div className="col-6 col-sm-4 col-md-3 col-lg-2">
                                                <div className="pf-box">
                                                    <h6>Parking Type</h6>
                                                    <p>{pdata.parking_type}</p>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="row">
                                            <div className="col-6 col-sm-4 col-md-3 col-lg-2">
                                                <div className="pf-box">
                                                    <h6>Patio</h6>
                                                    <p>{pdata.patio === "N/A"?pdata.patio:pdata.patio+" SF"}</p>
                                                </div>
                                            </div>
                                            <div className="col-6 col-lg-3">
                                                <div className="pf-box">
                                                    <h6>Playgroung</h6>
                                                    <p>{pdata.playground}</p>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                {pdata.yt_url?<div className="video-item">
                                    <iframe width="560" height="420" src={pdata.yt_url} title="YouTube video player" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
                                </div>:""}
                                {pdata.want_to === "Sell"?<div className="loan-calculator">
                                    <h4>Loan Calculator</h4>
                                    <form className="row" autoComplete="off" onSubmit={loanCalc}>
                                        <div className="col-md-3">
                                            <div className="form_wr_input">
                                                <input value={purprice} onChange={(e)=>setPurprice(e.target.value)} className="form_input_style" type="text" placeholder="Purchase Price" id="lc-price" />
                                                <span className="form_err">{calcError.epurprice}</span>
                                            </div>
                                        </div>
                                        <div className="col-md-3">
                                            <div className="form_wr_input">
                                                <input value={irate} onChange={(e)=>setIrate(e.target.value)} className="form_input_style" type="text" placeholder="Interest Rate" id="lc-interest" />
                                                <span className="form_err">{calcError.eirate}</span>
                                            </div>
                                        </div>
                                        <div className="col-md-3">
                                            <div className="form_wr_input">
                                                <input value={downPay} onChange={(e)=>setDownpay(e.target.value)} className="form_input_style" type="text" placeholder="Down Payment" id="lc-dpay" />
                                                <span className="form_err">{calcError.edownPay}</span>
                                            </div>
                                        </div>
                                        <div className="col-md-3">
                                            <div className="form_wr_input">
                                                <input value={monterms} onChange={(e)=>setMonterms(e.target.value)} className="form_input_style" type="text" placeholder="Terms Months" id="lc-months" />
                                                <span className="form_err">{calcError.emonterms}</span>
                                            </div>
                                        </div>
                                        <div className="col-md-12">
                                            <div className="loan-cal-result">Monthly Installment: <span id="lc-result"><i className="fas fa-rupee-sign"></i>{Emi}.00</span></div>
                                            <div className="text-left text-sm-right">
                                                <button className="site-btn sb-big" id="lc-submit">Calculate</button>
                                            </div>
                                        </div>
                                    </form>
                                </div>:""}
                            </div>
                            <div className="col-lg-4 col-md-8 sidebar">
                                {pdata.g_map_url?<div className="map-widget">
                                    <iframe src={pdata.g_map_url} style={{border: 0}} allowFullScreen="" loading="lazy" title="map"></iframe>
                                </div>:""}
                                <div className="agent-widget">
                                    <form className="inq-form" autoComplete="off" onSubmit={quickInq}>
                                        {ingFrm.open?<div className="row">
                                            <div className="col-sm-12">
                                                <div className="form_wr_input">
                                                    <input type="text" className="form_input_style" onChange={(e)=>setInqName(e.target.value)} placeholder="Name" />
                                                    <span className="form_err">{inqForm.inqName}</span>
                                                </div>
                                            </div>
                                            <div className="col-sm-12">
                                                <div className="form_wr_input">
                                                    <input type="text" className="form_input_style" onChange={(e)=>setInqPhone(e.target.value)} placeholder="10 Digit Phone Number" />
                                                    <span className="form_err">{inqForm.inqPhone}</span>
                                                </div>
                                                <button disabled={ingFrm.btn} className="site-btn sb-big">Quick Inquiry</button>
                                            </div>
                                            <div className="col-sm-12 server_error">
                                                {srvErr && srvErr.map(err =>
                                                    <div key={err.msg} className="form_err">{err.msg}</div>
                                                )}
                                            </div>
                                        </div>:<p className='inq_frm_msge'>{ingFrm.msge}</p>}
                                        <div className='row'>
                                            {shortlist?<div className="col-sm-12">
                                                <a className="offer-btn disabled">Shortlisted</a>
                                            </div>:
                                            <div className="col-sm-12">
                                                {localUser?<button onClick={shrtlstProp} disabled={shrtBtn} className="offer-btn">Shortlist</button>:<Link to="/login" className="offer-btn">Shortlist</Link>}
                                            </div>}
                                        </div>
                                    </form>
                                </div>
                            </div>
                        </div>
                    </div>
                </section></div>:<div>
                    <section className="page-top-section set-bg" style={{ backgroundImage: "url('/img/page-top-bg.jpg')"}}>
                        <div className="page-top-warp">
                            <SearchBox />
                        </div>
                    </section>
                    <section id="main-container" className="main-container">
                        <div className="container">

                            <div className="row">

                                <div className="col-12">
                                    <div className="error-page text-center">
                                        <div style={{height:"60px"}}>&nbsp;</div>
                                        <h5 className="not_fnd_hd">OOPS... Property Not Found!</h5>
                                        <p className="not_fnd_body">
                                            Try to Search some other Property<br />
                                            <Link to="/" className="readmore-btn">Back to Home Page</Link>
                                        </p>
                                    </div>
                                </div>

                            </div>
                        </div>
                    </section>
                </div>}</div>}
        </>
    )
}